@media (max-width: 767px) {
  .about__img-wrapper {
    margin-bottom: 40px;
    aspect-ratio: 320/312;
  }
}

.about__img-wrapper img {
  width: 100%;
}
@media (max-width: 767px) {
  .about__img-wrapper img {
    height: 100%;
    width: auto;
    max-width: none;
  }
}

.about__video-wrapper {
  position: absolute;
  left: 0;
  bottom: -115px;
}
@media (max-width: 1199px) {
  .about__video-wrapper {
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 50%);
  }
}
@media (max-width: 767px) {
  .about__video-wrapper {
    position: relative;
    left: 0;
    transform: none;
  }
}

.video {
  width: 364px;
  height: 228px;
  overflow: hidden;
}
@media (max-width: 1199px) {
  .video {
    width: 39.2330383481%;
    max-width: 364px;
    height: auto;
    aspect-ratio: 266/167;
  }
}
@media (max-width: 767px) {
  .video {
    width: 100%;
    max-width: none;
    aspect-ratio: 320/167;
  }
}

.video__link {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.video__link::before {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  content: "";
  background-color: rgba(20, 54, 122, 0.5);
}

.video img {
  max-width: none;
  height: 100%;
  width: auto;
  margin-left: -7px;
}
@media (max-width: 767px) {
  .video img {
    width: 100%;
    height: auto;
    margin-left: 0;
  }
}

.video iframe {
  display: none;
  width: 100%;
  height: 100%;
}

.video__play {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 100px;
  background-image: url("../img/background/play.png");
  background-repeat: no-repeat;
  background-size: cover;
  border: 0;
  padding: 0;
  background-color: transparent;
  transform: translate(-50%, -50%);
  transition: 0.3s ease;
  cursor: pointer;
}
@media (max-width: 1199px) {
  .video__play {
    width: 73px;
    height: 73px;
  }
}
@media (hover: hover) {
  .video__play:hover:not(.focus-visible) {
    transform: translate(-50%, -50%) scale(1.2);
  }
}

.is-active .video__link {
  display: none;
}

.is-active .video__play {
  display: none;
}

.is-active iframe {
  display: block;
}

.main-footer {
  min-height: 108px;
  background-color: #030a1a;
}

